<template>
  <div class="page" v-show="load">
    <div class="page__content">
      <div class="page__header">
        <breadcrumbs />
        <h1 class="page__title">
          {{ event.title }}
        </h1>
      </div>

      <div class="event">
        <div class="event__header">
          <div class="event__block-first">
            <div class="event__genre" v-if="getCategory">{{ getCategory }}</div>
            <div class="event__location" v-if="getPlace">{{ getPlace }}</div>
            <div class="event__date" v-if="getDate">{{ getDate }}</div>
          </div>
          <div class="event__block-second">
            <div
              class="event__bookmark"
              v-if="event.is_favorite"
              @click="onDeleteFavorite"
            >
              <svg width="20" height="20">
                <use href="@/assets/img/sprite.svg#icon-bookmark"></use>
              </svg>
              В избранном
            </div>
            <div
              class="event__bookmark"
              v-if="!event.is_favorite"
              @click="onCreateFavorite"
            >
              <svg width="20" height="20">
                <use href="@/assets/img/sprite.svg#icon-bookmark-border"></use>
              </svg>
              В избранном
            </div>
            <div class="event__free">Бесплатно</div>
            <!--            <router-link to="/tickets" class="event__ticket-btn button-primary">-->
            <!--              Билеты-->
            <!--            </router-link>-->
          </div>
        </div>
      </div>
      <div class="event__body">
        <div class="event__image" v-if="event.title_picture.url">
          <img :src="event.title_picture.url" alt="" />
        </div>

        <div class="event__description">
          <div class="event__col-left">
            <h2 class="event__title">О событии</h2>
            <div v-html="event.body"></div>
          </div>
          <div class="event__col-right">
            <h2 class="event__title">
              Рейтинг
            </h2>
            <div class="event__rating">
              <star-rating
                :read-only="true"
                :star-size="28"
                :rating="event.rating"
              />
              <div class="event__voting">
                {{ getVoteCount }}
              </div>
            </div>
          </div>
        </div>
        <image-gallery
          v-if="event.images.length"
          :images="event.images"
        ></image-gallery>
      </div>

      <comments :comments="event.comments" @add-comment="addNewComment" />
    </div>
  </div>
</template>

<script>
import ImageGallery from "../components/ImageGallery";
import Comments from "../components/Comments";
import StarRating from "vue-star-rating";
import axios from "axios";
import mixin from "../mixin/mixin";
import qs from "qs";

export default {
  name: "EventPage",
  components: { Comments, ImageGallery, StarRating },
  mixins: [mixin],
  data() {
    return {
      event: {
        place: {
          title: ""
        },
        title_picture: {
          url: ""
        },
        images: []
      },
      comments: [],
      load: false
    };
  },
  async mounted() {
    await this.loadData();
  },

  methods: {
    async loadData() {
      let load = this.$loading.show();

      let url = new URL(`${process.env.VUE_APP_API_URL}/v1/event/view`);
      url.searchParams.set("id", this.$route.params.id);

      await axios
        .get(url)
        .then(response => {
          this.event = response.data;
          this.load = true;
          load.hide();
        })
        .catch(error => {
          if (error.response.status === 404) {
            this.$toasted.global.error("Событие не найдено");
            this.$router.push("/playbill");
          } else {
            this.$toasted.global.error(error.response.data.message);
          }
          if (!error.response) {
            this.$toasted.global.error("Нет соединения с сервером");
            load.hide();
          }
          load.hide();
        });
    },
    async addNewComment(comment) {
      axios
        .post(
          `${process.env.VUE_APP_API_URL}/v1/event-comment/create`,
          qs.stringify({ event_id: this.event.id, body: comment.body })
        )
        .then(result => {
          this.event.comments.push(result.data);
        })
        .catch(error => {
          console.log(error.request);
        });
    },
    onCreateFavorite() {
      let form = qs.stringify({
        entity_type: "event",
        entity_id: this.event.id
      });
      axios
        .post(`${process.env.VUE_APP_API_URL}/v1/favorite/create`, form)
        .then(response => {
          this.event.is_favorite = response.data.id;
        })
        .catch(error => {
          if (!error.response) {
            this.$toasted.global.error("Нет соединения с сервером");
          }
          console.log(error);
        });
    },
    onDeleteFavorite() {
      let url = new URL(`${process.env.VUE_APP_API_URL}/v1/favorite/delete`);
      url.searchParams.append("id", this.event.is_favorite);
      axios
        .delete(url)
        .then(() => {
          this.event.is_favorite = 0;
        })
        .catch(error => {
          if (!error.response) {
            this.$toasted.global.error("Нет соединения с сервером");
          }
          console.log(error);
        });
    }
  },
  computed: {
    getCategory() {
      return this.event.categories
        ? this.event.categories.map(item => item.title).join(" ● ")
        : "";
    },
    getPlace() {
      return this.event.place && this.event.place.title;
    },
    getDate() {
      return this.event.begin_time
        ? this.$options.filters.formatDate(this.event.begin_time)
        : "";
    },
    getVoteCount() {
      return (
        this.event.vote_count +
        " " +
        this.getNoun(this.event.vote_count, "голос", "голоса", "голосов")
      );
    }
  }
};
</script>

<style scoped></style>
