<template>
  <div class="image-gallery">
    <div
      class="image-gallery__item"
      v-for="(img, index) in images"
      :key="index"
      v-show="index < showImages"
    >
      <img v-img:group :src="img.url" />
      <div
        class="image-gallery__count"
        v-if="index == showImages - 1 && countHideImages > 0"
      >
        +{{ countHideImages }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ImageGallery",
  props: {
    images: {
      type: Array,
      default: () => []
    },
    showImages: {
      type: Number,
      default: 4
    }
  },
  data() {
    return {
      imagesArray: []
    };
  },
  computed: {
    countHideImages() {
      return this.images.length - this.showImages;
    }
  },
  mounted() {
    this.imagesArray = this.images.slice(0, this.showImages);
  }
};
</script>

